body {
  background-color: #ffffff;
  font-family: Helvetica, Arial, sans-serif;
  color: #333333;
  margin-left: auto;
  margin-right: auto;
  display: block;
  max-width: 600px;
  line-height: 1.5;
  font-size: 16px;
}

h1, h2, h3, h4, h5, h6, p, ol, ul {
  margin-top: 0;
}

h1 {
  font-size: 34px;
}

h2 {
  font-size: 30px;
}

h3 {
  font-size: 28px;
}

h4 {
  font-size: 24px;
}

h5 {
  font-size: 20px;
}

h6 {
  font-size: 18px;
}

h1, h2, h3, h4, h5, h6 {
  margin-bottom: 10px;
}

p {
  margin-bottom: 10px;
}

hr {
  border-top: 1px solid #333333;
  margin-top: 20px;
  margin-bottom: 20px;
}

ul, ol {
  padding-left: 2px;
}

img {
  max-width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
}